.featured_property_sec {
  h4 {
    font-family: OpenSans-Bold;
    font-size: $font-size-md;
    text-align: left;
  }
  .btn-primary {
    @include button($primary);
  }
  .card-container {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    @include transitionEase;
  }
  .villa_sale_card {
    min-width: calc((100% - 48px) / 3);
    border: 1px solid #f0f0f1;
    border-radius: 10px;
    &:not(:nth-child(3n + 3)) {
      margin-right: 24px;
    }
    img {
      width: 100%;
      height: 200px;
      object-fit: cover;
      margin-bottom: 10px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
  }
  .villa_detail {
    padding: 20px;
    .villa_heading {
      font-size: $font-size-md-2;
      font-family: OpenSans;
      line-height: 1.3;
      text-align: left;
    }
    .villa_price {
      font-size: $font-size-regular;
      font-family: OpenSans-Bold;
      font-weight: bold;
      text-align: left;
    }
    .villa_address {
      font-size: $font-size-small;
      font-family: OpenSans;
      line-height: 1.3;
      text-align: left;
    }
  }
  .villa_facilities {
    font-family: OpenSans;
    ul {
      @include flexBetween;
      li {
        font-size: $font-size-small;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        &:last-child svg {
          height: 20px;
        }
      }
      span {
        margin-right: 5px;
        svg {
          width: 25px;
          height: 25px;
          path {
            fill: $dark;
          }
          &:hover {
            path {
              fill: $primary;
            }
          }
        }
      }
    }
  }
  @include breakpoint(map-get($breakpoints, "midbreak")) {
    .card-container {
      gap: 10px;
    }
    .villa_sale_card {
      width: calc((100% - 40px) / 2);
      gap: 10px;
      &:not(:nth-child(3n + 3)) {
        margin-right: 0px;
      }
    }
  }
  @include breakpoint(map-get($breakpoints, "tablet")) {
      .villa_sale_card {
        width: calc((100% - 10px) / 2);
        &:not(:nth-child(3n + 3)) {
          margin-right: 0px;
        }
      }
      .villa_detail {
        padding: 14px;
        .villa_heading {
          font-size: $font-size-md-2;
        }
      }
      .villa_facilities {
        ul {
          li {
            font-size: 10px;
            &:last-child svg {
              height: 14px;
            }
          }
          span {
            svg {
              width: 18px;
              height: 18px;
              margin-right: 6px;
            }
          }
        }
      }
  }
  @include breakpoint(map-get($breakpoints, "mobile")) {
    h4 {
      text-align: center;
      font-size: $font-size-md-2;
    }
    .btn-primary{
      padding: 10px 20px;
      font-size: $font-size-small;
    }

    .card-container {
      gap: 0px;
      .villa_sale_card {
        width: 100%;
        img{
          height: 100px;
        }
      }
    }
    .villa_detail {
      padding: 14px;
      .villa_heading {
        font-size: $font-size-regular;
      }
      .villa_price {
        font-size: $font-size-small;
      }
      .villa_address {
        font-size: 12px;
      }
    }
  }
}
